import { useState, useEffect, useRef } from 'react'
import styled from 'styled-components'

const VirtualTextArea = styled.div`
  caret-color: transparent;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 16px;

  &::after {
    content: ''; /* OK */
    animation: blink 1s infinite; /*  */
  }

  /*  */
  @keyframes blink {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    }
    51% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
`
const breakLetter = ['', '']

const statement = [
  ' ',
  '',
  'Java',
  '',
  'SI',
  '',
  '',
  'SI',
  'Java',
  '',
  'B2C',
  '',
  '',
  '20187',
  '',
  ' ',
  '',
  'TypeScript/React',
  '',
  'Java(JDK1.0)C#()PHP/Perl/Python(',
  'ReactTypeScriptHTMLCSS',
  'CVS, Subversion, Git',
  '',
  '',
  'Web',
]

const sleep = (second: number) =>
  new Promise((resolve) => setTimeout(resolve, second * 1000))

export const TextArea: React.FC = () => {
  const ref = useRef<HTMLDivElement>(null)
  const [isReady, setIsReady] = useState(false)
  const [text, setText] = useState('')

  const showLetter = async (letter: string): Promise<void> => {
    if (!isReady) return
    setText((text) => text.concat(letter))
    ref.current?.focus()
    const wait = breakLetter.includes(letter) ? 0.5 : 0.05
    await sleep(wait)
  }

  const showLine = async (str: string): Promise<void> => {
    for (let i = 0; i < str.length; i++) {
      await showLetter(str[i])
    }
    await sleep(1)
    setText((text) => text.concat('<br/>'))
  }

  const showText = async (): Promise<void> => {
    ref.current?.focus()
    await sleep(2)
    for (let i = 0; i < statement.length; i++) {
      await showLine(statement[i])
    }
  }

  useEffect(() => {
    if (isReady) {
      showText()
    }
  }, [isReady])

  useEffect(() => {
    setText('')
    setIsReady(true)
    return () => {
      setIsReady(false)
    }
  }, [ref])

  return (
    <VirtualTextArea
      ref={ref}
      dangerouslySetInnerHTML={{
        __html: text,
      }}
    ></VirtualTextArea>
  )
}
